import { Toast } from 'vant'
import { getCartList, changeCount, delSelect } from '@/api/cart'
export default {
  namespaced: true, // 开启命名空间
  state () {
    return {
      cartList: [] // 购物车列表
    }
  },
  mutations: {
    setCartList (state, payload) {
      state.cartList = payload
    },
    // 选中状态
    toggleCheck (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    toggleAllCheck (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    changeCount (state, { goodsId, value }) {
      const payload = state.cartList.find(item => item.goods_id === goodsId)
      payload.goods_num = value
    }
  }, // 同步
  actions: {
    async getCartAction (context) {
      const res = await getCartList()
      res.data.data.list.forEach(element => {
        element.isChecked = true
      })
      context.commit('setCartList', res.data.data.list)
    },
    async changeCountAction (context, payload) {
      const { goodsId, value, skuId } = payload
      context.commit('changeCount', {
        goodsId,
        value
      })
      await changeCount(goodsId, value, skuId)
    },
    // 删除购物车数据
    async delSelect (context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map(item => item.id)
      await delSelect(cartIds)
      setTimeout(() => {
        Toast.success('删除成功')
      }, 0)
      // 重新拉取最新的购物车数据 (重新渲染)
      context.dispatch('getCartAction')
    }
  }, // 异步
  getters: {
    // 累加
    totalPrice (state) {
      return state.cartList.reduce((sum, item, index) => sum + item.goods_num, 0)
    },
    // 选中的商品项
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 选中的商品总数量
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item, index) => sum + item.goods_num, 0)
    },
    // 选中的商品总价格
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, item, index) => sum + item.goods_num * item.goods.goods_price_min, 0)
    },
    // 是否全选
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  },
  modules: {
  }
}
