import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: () => import('@/views/layout/index.vue'),
      redirect: '/home',
      children: [
        {
          path: 'home',
          component: () => import('@/views/layout/home')
        },
        {
          path: 'category',
          component: () => import('@/views/layout/category')
        },
        {
          path: 'cart',
          component: () => import('@/views/layout/cart')
        },
        {
          path: 'user',
          component: () => import('@/views/layout/user')
        }
      ]
    },
    { path: '/login', component: () => import('@/views/login/index') },
    { path: '/search', component: () => import('@/views/search/index') },
    { path: '/searchlist', component: () => import('@/views/search/searchList') },
    { path: '/prodetail/:id', component: () => import('@/views/prodetail/index') },
    { path: '/pay', component: () => import('@/views/pay/index') },
    { path: '/myorder', component: () => import('@/views/myorder/index') }
  ]
})

// 1. to   往哪里去， 到哪去的路由信息对象
// 2. from 从哪里来， 从哪来的路由信息对象
// 3. next() 是否放行
//    如果next()调用，就是放行
//    next(路径) 拦截到某个路径页面

// 验证token放行页面
const authUrl = ['/pay', '/myorder']
// 路由守卫
router.beforeEach((to, from, next) => {
  // 非权限页面直接放行
  if (!authUrl.includes(to.path)) {
    next()
    return
  }
  // 权限页面需要验证token
  const token = store.getters.token
  if (token) {
    next()
  } else {
    next('/login')
  }
})

export default router
