import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'
import cart from './modules/cart'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
  },
  getters: {
    token (state) {
      return state.user.userInfo.token
    }
  },
  mutations: {
  },
  actions: {
  },
  // 挂载到vuex上
  // modules 配置项
  // 用于将 store 分割成模块（module）
  // 每个模块拥有自己的 state、mutation、action、getter、甚至是嵌套子模块
  // 模块内部的 action、mutation 和 getter 是注册在全局命名空间中的
  // 同样，根节点上的 mutation 和 getter 是注册在全局命名空间中的
  modules: {
    user,
    cart
  }
})
