import { getInfo, setInfo } from '@/utils/storage'
export default {
  // 命名空间
  namespaced: true,
  // 模块化
  state () {
    return {
      userInfo: getInfo()
    }
  },
  // 计算属性
  mutations: {
    setUserInfo (state, payload) {
      state.userInfo = payload
      setInfo(payload)
    }
  },
  // 异步操作
  actions: {
    logout (context) {
      context.commit('setUserInfo', {})
      context.commit('cart/setCartList', [], { root: true })
    }
  }

}
